* {
    box-sizing: border-box;
}

body {
    margin:0;
    padding:0;
    background-color: #eff3f7;
    height:100%;
}

html,
body,
#root,
.container {
    height:100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding:50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color:#adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.25);

}

.movie img {
    position: relative;
    top: -50px;
    max-width: 180px;
    width:100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.25);
}

.movie .movie_title,
.movie .movie_year {
    margin:0;
    font-weight: 300;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres {
    list-style: none;
    padding:0;
    margin :0;
    display:flex;
    margin:5px 0px;
}

.movie_genres li,
.movie .movie_year {
    margin-right: 10px;
    font-size:14px;
}